@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html {
  background: #1B1C22;
  color: grey;
  font-family: 'Montserrat', sans-serif;
}

.stationsPanel {
  display: flex;
  flex-wrap: wrap;
  padding: 60px 10% 100px;
  justify-content: center;
}

.station {
  background: #25262C;
  margin: 15px 10px;
  padding: 25px 30px 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
  transition: ease .2s;
  cursor: pointer;
}

.station:hover {
  background: #35394f;
  transform: scale(1.1);
}

.station img {
  width: 125px;
  min-height: 120px;
  padding-bottom: 15px;
}

.station .tags {
  display: flex;
  text-transform: capitalize;
}

.station .tags p {
  margin: 0;
  margin-right: 10px;
}

.station .infos>p {
  font-size: 0.8rem;
  font-weight: 700;
  color: lightgrey;
  margin: 3px 0 5px;
}

.playerBar {
  display: flex;
  background: #0f0f10ed;
  position: fixed;
  bottom: -5px;
  left: 0;
  right: 0;
  z-index: 99;
  padding: 5px 0px 7px 5px;

}

.playerBar img {
  width: 50px;
  height: 50px;
}

.radioInfo {
  display: flex;
  align-items: center;
  padding-right: 30px;
  border-right: 1px solid rgb(48, 48, 48);
}

.radioInfo p {
  font-size: 0.9rem;
  font-weight: 700;
  color: lightgrey;
  margin: 0;
  margin-left: 30px;
}

.audioControl {
  font-size: 2.5rem;
  color: lightgrey;
  margin: 0 auto;
  display: flex;
  align-items: center;
  margin-left: 30px;
}


.musicIcon {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 25px;
  height: 30px;
  margin-left: 30px;

}

.musicIcon span {
  width: 5px;
  height: 100%;
  background-color: orange;
  border-radius: 3px;

  content: '';
}

@keyframes bounce {
  10% {
    transform: scaleY(0.3);
    /* start by scaling to 30% */
  }

  30% {
    transform: scaleY(1);
    /* scale up to 100% */
  }

  60% {
    transform: scaleY(0.5);
    /* scale down to 50% */
  }

  80% {
    transform: scaleY(0.75);
    /* scale up to 75% */
  }

  100% {
    transform: scaleY(0.6);
    /* scale down to 60% */
  }
}

.musicIcon.bounce span {
  animation: bounce 2.2s ease infinite alternate;

}

.musicIcon.bounce span:nth-of-type(2) {

  animation-delay: -2.2s;
  /* Start at the end of animation */

}

.musicIcon.bounce span:nth-of-type(3) {

  animation-delay: -3.7s;
  /* Start mid-way of return of animation */

}

.searchBar {
  display: flex;
  flex-wrap: wrap;
  background: #161617ed;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
}


h1 {
  font-size: 1.4rem;
  color: rgb(216, 216, 216);
  margin-left: 20px;
  cursor: pointer;
}

h1 span {
  color: rgb(255, 166, 0);
  ;
}

form {
  display: flex;
  align-items: stretch;
  margin-left: 30px;
}

h2 {
  margin-top: 140px;
  text-align: center;
}

.searchBar input {
  background: #25262C;
  border: none;
  color: grey;
  font-size: 1.2rem;
  padding: 10px 0px 8px 20px;
  text-transform: capitalize;
}

.searchBar input:focus {
  outline: none;
}

.searchBar form div {
  font-size: 1.6rem;
  padding: 0px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1B1C22;
  transition: ease .2s;
  cursor: pointer;
}

.searchBar form div:hover {
  background: #35394f;

}

.searchBar svg {
  font-size: 1.4rem;
}

@media all and (max-width: 700px) {
  .searchBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
  }

  h1 {
    margin-left: 0;
  }

  .searchBar form {
    margin-left: 0;
  }

  .searchBar input {
    margin-left: 0;
  }
}